/*------ navbar start ------*/
.nav-bar {
  background-color: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  padding: 8px 0;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logo-container {
    width: 196px;
    .logo {
      width: 196px;
    }
  }
  .icons {
    width: 196px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .menu-icon {
      display: none;
    }
    .cart-icon {
      position: relative;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: #29933a;
      font-size: 24px;
      color: rgb(255, 255, 255);
      transition: all .3s ease-in-out;
      .counter {
        position: absolute;
        top: -9px;
        right: -7px;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: #b79649;
        border-style: solid;
        border-width: 2px;
        border-color: rgb(255, 255, 255);
        color: #fff;
        font-family: ar-bold;
        font-size: 14px;
        padding: 0px 0 0 0;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
      }
      &:hover {
        background-color: #b79649;
        .counter {
          background-color: #29933a;
          transform: rotateY(360deg);
        }
      }
    }
  }
  .lang-switcher {
    font-family: ar-reg;
    font-size: 16px;
    color: #999d9e;
    margin: 0 0 0 30px;
    position: relative;
    a {
      transition: all .3s ease-in-out;
    }
    &:hover {
      a {
        color: #29933a;
      }
    }
  }
}


/*------ navbar end ------*/



/*------ navlist start ------*/
.nav-list {
  .list {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      font-family: ar-reg;
      font-size: 17px;
      color: #000;
      text-transform: uppercase;
      margin: 0 13px;
      transition: all .3s ease-in-out;
      &:hover {
        color: #29933a;
      }
    }
  }
}
/*------ navlist end ------*/





/*------ header start ------*/
.header {
  .slide {
    img {
      transition: all .5s ease-in-out;
      &:hover {
        transform: scale(1.03);
      }
    }
  }
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        display: none;
      }
    }
   
}

/*------ header end ------*/



.fixed-icon {
  position: fixed;
  right: 25px;
  bottom: 30px;
  z-index: 9;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  color: rgb(255, 255, 255);
  font-size: 38px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  button {
    color: #fff;
  }
  &.one {
    bottom: 99px;
    background-color: rgb(239, 81, 31);
    font-size: 28px;
  }
  &.two {
    background-color: rgb(12, 194, 67);
  }
  &:hover {
    background-color: #29933a;
  }
}



/*------ our program start ------*/
.our-program {
  margin: 70px 0 0 0;
  .title {
    margin: 0 0 50px 0;
    position: relative;
    font-family: ar-md;
    font-size: 24px;
    color: rgb(0, 0, 0);
    text-transform: uppercase;
    text-align: center;
    &::after {
      content: "";
      width: 28px;
      height: 2px;
      background-color: rgb(183, 150, 73);
      position: absolute;
      bottom: -18px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .program {
    width: 556px;
    height: 674px;
    border-radius: 20px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
    padding: 23px 20px;
    margin: 0 auto;
    .program-img {
      overflow: hidden;
      border-radius: 13px;
    }
    .program-title {
      font-family: ar-reg;
      font-size: 24px;
      color: #000;
      text-align: center;
      margin: 30px 0 0 0;
      padding: 0;
    }
    .program-cost {
      font-family: ar-bold;
      font-size: 30px;
      color: rgb(41, 147, 58);
      text-align: center;
      margin: 8px 0 24px 0;
      padding: 0;
    }
    .timer {
      display: flex;
      justify-content: center;
      align-items: center;
      .day, .hour, .min, .sec {
        width: 70px;
        height: 55px;
        background-color: rgb(239, 81, 31);
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border-right: 2px solid #fff;
        padding: 7px 0 0 0;
        span {
          font-family: ar-bold;
          font-size: 18px;
          color: rgb(255, 255, 255);

          display: block;
          max-height: 20px;
          padding: 0;
          margin: 0;
          &:last-child {
            font-family: ar-reg;
            font-size: 17px;
            margin: 0 0 13px 0;
          }
        }
      }
    }
    .btns {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 24px 0 40px 0;
      .program-counter {
        width: 215px;
        height: 50px;
        border-style: solid;
        border-width: 2px;
        border-color: rgb(230, 229, 227);
        border-radius: 26px;
        background-color: rgb(255, 255, 255);
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 11px 3px 11px;
        margin: 0 0 0 18px;
        button {
          font-size: 27px;
          color: rgb(129, 132, 133);
          transition: all .3s ease-in-out;
          &:hover {
            color: #29933a;
          }
        }
        span {
          font-family: ar-reg;
          font-size: 24px;
          color: rgb(0, 0, 0);
        }
      }
      .add-to-cart {
        width: 215px;
        height: 50px;
        background-image: linear-gradient(90deg, #29933b, #29933b, #b7974a, #b7974a);
        background-size: 300%;
        border-radius: 26px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
        button {
          font-size: 22px;
          color: rgb(255, 255, 255);
          padding: 0 7px;
        }
        span {
          font-family: ar-md;
          font-size: 20px;
          color: #fff;
        }
        &:hover {
          background-position-x: 100%;
        }
      }
    }
  }
}
/*------ our program end ------*/




/*------ footer start ------*/
.footer {
  background-color: #29933a;
  padding: 20px 0;
  margin: 130px 0 0 0;
  .copy-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .copyrights {
    font-family: ar-reg;
    font-size: 15px;
    color: #fff;
    width: 160px;
  }
  .designed-by {
    width: 160px;
    font-family: ar-reg;
    font-size: 15px;
    color: #fff;
    img {
      margin: 0 5px;
    }
  }
}
/*------ footer end ------*/








.fixed-menu {
  display: none;
}


/*------ responsive ------*/
@media (min-width: 1600px) {
  .owl-carousel .owl-item img {
    width: 100%;
  }
}
@media (max-width: 992px) {
  .pre-loader .content {
    width: 230px;
  }
  .nav-bar {
    position: relative;
  }
  .nav-bar .lang-switcher {
    display: none;
  }
  .nav-bar .icons .menu-icon {
    display: block;
    position: absolute;
    top: 50%;
    right: 50px;
    transform: translateY(-50%);
    font-size: 24px;
    color: rgb(76, 76, 76);
    
  }
  .nav-list {
    display: none;
  }



  .fixed-menu .lang-switcher {
    font-family: ar-reg;
    font-size: 15px;
    color: #444;
    margin: 0 0 0 0;
    position: relative;
  }

  .fixed-menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 999;
    .menu {
      width: 260px;
      height: 100%;
      position: absolute;
      top: 0;
      right: -280px;
      background-color: #fff;
      padding: 20px 20px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      &.show {
        right: 0;
      }
    }
    .menu-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .close {
        opacity: 1;
        color: #444;
        font-size: 17px;
        position: absolute;
        top: 20px;
        left : 15px;
      }
    }
    .main-list {
      margin: 35px 0px;
      transform: translateX(-50%);
      transition: all .5s ease-in-out;
      li {
        font-family: ar-md;
        font-size: 16px;
        color: #444;
        margin: 15px 0;
      }
      &.show {
        transform: translateX(0%);
      }
    }
  }


  .fixed-icon {
    width: 55px;
    height: 55px;
  }
  .fixed-icon.one {
    left: 10px;
    bottom: 10px;
    right: unset;
  }
  .fixed-icon.two {
    right: 10px;
    bottom: 10px;
  }




  .footer {
    padding: 34px 0;
    .copy-container {
      flex-direction: column;
      .copyrights {
        order: 2;
        padding: 30px 0 8px 0;
        text-align: center;
      }
      .payment {
        order: 1;
      }
      .designed-by {
        order: 3;
        text-align: center;
      }
    }
  }


  


  
}



@media (max-width: 480px) {
  .nav-bar .icons .menu-icon {
    right: 10px;
  }
  .nav-bar .logo-container .logo {
    margin: 0 30px 0 0;
    width: 158px;
  }
  .nav-bar .icons .cart-icon .counter {
    padding: 4px 0 0 0;
  }
  .our-program {
    margin: 40px 0 0 0;
    .container {
      padding: 0;
    }
  }
  .our-program .title {
    font-size: 20px;
    margin: 0 0 40px 0;
    &::after {
      bottom: -17px;
    }
  }
  .our-program .program {
    width: 346px;
    height: 577px;
    padding: 13px 13px;
    margin: 0 auto;
  }
  .our-program .program .btns {
    flex-direction: column;
    margin: 24px 0 0 0;
    .program-counter {
      margin: 0 0 13px 0;
    }
  }
  .footer {
    margin: 60px 0 0 0;
  }
}

@media (max-width: 375px) {
  
}


/*------ direction ------*/
html[dir="ltr"] {
  .nav-bar .lang-switcher {
    margin: 0 30px 0 0;
  }
  .fixed-icon {
    right: unset;
    left: 25px;
  }
  .our-program .program .btns .program-counter {
    margin: 0 18px 0 0;
  }
  .footer .copyrights {
    width: 250px;
  }
  .footer .designed-by {
    width: 250px;
  }
  @media (max-width: 992px) {
    .nav-bar .icons .menu-icon {
      right: unset;
      left: 50px;
    }
    .fixed-menu .menu {
      right: unset;
      left: -280px;
      &.show {
        right: unset;
        left: 0;
      }
    }
    .fixed-menu .menu-header .close {
      left: unset;
      right: 15px;
    }
    .fixed-icon.one {
      left: unset;
      right: 10px;
    }
    .fixed-icon.two {
      right: unset;
      left: 10px;
    }

  }
  @media (max-width: 480px) {
    .nav-bar .icons .menu-icon {
      left: 10px;
    }
    .nav-bar .logo-container .logo {
      margin: 0 0 0 30px;
    }
    .our-program .program .btns .program-counter {
      margin: 0 0 13px 0;
    }
  }
}